body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}



.wrapper {
  padding-top: 50px;
  background: #ECEFF1;
  height: 100%;
  display: flex;
  flex-flow: row wrap;
  padding: 20px;
  justify-content: space-around;
  align-content: flex-start;
  overflow: auto;
}

nav {
    width: 100%;
    text-align: center;
    background-color: forestgreen;
    color: white;
}

.navbar{
    width:100%;
}

.navbar-text {
    text-align: center;
}

a {
    color:white;
}

a:hover {
    color: white;
}

.navbar ul {
    width: 100%;
    padding: 0;
    margin: 0;
}
.navbar li {
    display: inline-block;
    width: 33.3%;
    text-align: center;
}

.fact-div p{
    text-align: center;
    font-style: italic;
    font-size: 14px;
    vertical-align: middle; 
    margin: 5px;
    
}

.fact-div{
    
    overflow: auto;

}
.App {
  text-align: center;
}

.park-div {
  height: 170px;
  width:170px;
  background-size: cover;
  margin: 5px;
  cursor: pointer;
  border: 3px solid darkgray;
  border-radius: 8px;
}

.park-div:hover {
  border: 3px solid forestgreen;
}

